.bannerContainer {
	z-index: 10;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	width: 100%;
	position: fixed;
	top: 0;
	left: 0;
	background: black;
	box-shadow: 0px 0px 30px 0px rgba(0,0,0,1);
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}
.bannerContainer a {
	text-decoration: none
}
.bannerContainer p {
	text-decoration: none;
	margin: 0 0 0 60px;
	font-size: 60px;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	color: white;
}
.bannerContainer nav {
	margin-right: 100px;
	display: flex;
	flex-direction: row;
}
.bannerContainer nav a {
	padding: 0 10px 0 10px;
	text-decoration: none;
	font-family: 'siegeLike';
	font-size: 30px;
	color: white;
	-webkit-transition: 0.3s ease;
	transition: 0.3s ease;
}
.bannerContainer nav a:hover {
	background-image: -webkit-linear-gradient(left, black 0%, #2A2A2A 45%, #2A2A2A 55%, black 100%);
	background-image: linear-gradient(to right, black 0%, #2A2A2A 45%, #2A2A2A 55%, black 100%);
	cursor: pointer;
	-webkit-transition: 0.3s ease;
	transition: 0.3s ease;
}
