.map {
	margin: 15px;
	position: relative;
	height: 225px;
	width: 500px;
	-webkit-transition: 0.5s;
	transition: 0.5s;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	border: 3px solid #373737;
}
.map:hover {
	-webkit-transition: 0.5s;
	transition: 0.5s;
	border: 3px solid #7D7676;
}
.map img {
	height: 100%;
	width: 100%;
	cursor: pointer;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}
.map p {
	position: absolute;
	margin: 0;
	bottom: 0;
	font-size: 30px;
	left: 50%;
	width: 100%;
	-webkit-transform: translateX(-50%) translateY(-50%);
	        transform: translateX(-50%) translateY(-50%);
	text-align: center;
	background: rgba(0,0,0,0.7);
	cursor: pointer;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	color: white;
}

