@media (max-width: 1060px) {
	.bottomFloorSkew {
		position: relative;
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		width: 20px;
		z-index: 1;
	}
	.bottomFloorSkew:before {
		display: block;
		-webkit-transform: skew(-30deg);
		        transform: skew(-30deg);
		width: 100%;
		height: 100%;
		content: '';
		position: absolute;
		background: #4E7878;
		top: -2px;
		right: 12px;
		z-index: -1;
		border: 2px solid black;
	}
	.bottomFloorSkew:after {
		display: block;
		width: 100%;
		height: 100%;
		content: '';
		position: absolute;
		background: #4E7878;
		top: 0px;
		left: 0px;
		z-index: -1;
	}
	.bottomFloorSkew:hover:before {
		-webkit-transition: ease 0.3s;
		transition: ease 0.3s;
		background: #223535;
	}
	.bottomFloorSkew:hover:after {
		-webkit-transition: ease 0.3s;
		transition: ease 0.3s;
		background: #223535;
	}
	.bottomFloorSkew.floorNavButton-active:before {
		-webkit-transition: ease 0.3s;
		transition: ease 0.3s;
		background: #223535;
	}
	.bottomFloorSkew.floorNavButton-active:after {
		-webkit-transition: ease 0.3s;
		transition: ease 0.3s;
		background: #223535;
	}
	.topFloorSkew {
		position: relative;
		display: flex;
		flex-direction: row;
		justify-content: flex-end;
		width: 20px;
		z-index: 1;
	}
	.topFloorSkew:after {
		display: block;
		width: 100%;
		height: 100%;
		content: '';
		position: absolute;
		background: #4E7878;
		top: 0px;
		left: 0px;
		z-index: -1;
	}
	.topFloorSkew:before {
		display: block;
		-webkit-transform: skew(30deg);
		        transform: skew(30deg);
		width: 100%;
		height: 100%;
		content: '';
		position: absolute;
		background: #4E7878;
		top: -2px;
		left: 12px;
		z-index: -1;
		border: 2px solid black;
	}
	.topFloorSkew:hover:before {
		-webkit-transition: ease 0.3s;
		transition: ease 0.3s;
		background: #223535;
	}
	.topFloorSkew:hover:after {
		-webkit-transition: ease 0.3s;
		transition: ease 0.3s;
		background: #223535;
	}
	.topFloorSkew.floorNavButton-active:after {
		-webkit-transition: ease 0.3s;
		transition: ease 0.3s;
		background: #223535;
	}
	.topFloorSkew.floorNavButton-active:before {
		-webkit-transition: ease 0.3s;
		transition: ease 0.3s;
		background: #223535;
	}
	.desktop {
		display: none;
	}
	.phone {
		display: flex;
	}
}
